import * as React from "react";
import Badge from "react-bootstrap/Badge";
import Col from "react-bootstrap/Col";
import Figure from "react-bootstrap/Figure";
import Row from "react-bootstrap/Row";
import classNames from "./classNames";
import {GameAction} from "./GameAction";

interface IMenuItemProps {
    icon: string;
    title: string;
    action: GameAction | undefined;
    description: string;
    disabled?: boolean;
    badge?: string | number;
    onClick(): void;
    onClickDisabled?(): void;
}

export default class MenuItemComponent extends React.PureComponent<IMenuItemProps> {
    public render() {
        const {icon, badge, action, title, description, disabled, onClick, onClickDisabled} = this.props;
        return <Row className={classNames("menu-item", disabled && "disabled", action && `action-${action}`)}
                    onClick={disabled ? onClickDisabled : onClick}>
            <Col xs={2} className="left">
                <Figure>
                    <Figure.Image src={icon}/>
                </Figure>
            </Col>
            <Col className={"right flex-grow"}>
                <Row>
                    <Col className={"title flex-grow"}>
                        {title}
                    </Col>
                    <Col xs={"auto"}>
                        {badge && <Badge variant={"success"} pill={true}>{badge}</Badge>}
                    </Col>
                </Row>
                <Row className={"description"}>
                    <Col className={"flex-fill"}>
                        <span>
                            {description}
                        </span>
                    </Col>
                </Row>
            </Col>
        </Row>;
    }
}
